html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Manjari", sans-serif;
}

a {
  text-decoration: none;
  color: #444;
}

a:hover {
  color: #1de9b6;
}

ul > li {
  list-style: none;
}

img {
  width: 100%;
}

::selection {
  background: #1de9b6;
}

h2 > span {
  background-color: #1de9b6;
}

.wrapper {
  display: grid;
  background: no-repeat url(./assets/images/background.png);
  background-size: 100%;
  grid-gap: 10px;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(3, 200px);
  color: #444;
}

.box {
  border: 1px solid #444;
  padding: 20px;
  font-size: 150%;
}

.a {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
  justify-self: stretch;
}

.b {
  grid-column: 3 / 4;
  grid-row: 2 / 3;
  justify-self: end;
  font-size: 130%;
}

.c {
  grid-column: 1 / 4;
  grid-row: 3 / 5;
  justify-self: auto;
  padding-top: 0;
}

.d {
  grid-column: 4 / 5;
  grid-row: 2 / 5;
  justify-self: auto;
}

.e {
  grid-column: 5 / 7;
  grid-row: 1 / 6;
  align-self: stretch;
  padding: 0;
  position: fixed;
  right: 0;
  width: 30%;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }

  .a {
    grid-column: 1;
    grid-row: 1;
    justify-self: auto;
  }

  .e {
    grid-column: 1;
    grid-row: 2;
    align-self: center;
    padding: 0;
    position: initial;
    width: 100%;
  }

  .c {
    grid-column: 1;
    grid-row: 3;
    justify-self: auto;
    padding-top: 0;
  }

  .d {
    grid-column: 1;
    grid-row: 4;
    justify-self: auto;
  }

  .b {
    grid-column: 1;
    grid-row: 5;
    justify-self: end;
  }

  ul {
    padding-left: 0;
  }
}
